@import url(https://fonts.googleapis.com/css2?family=Tahoma&display=swap);
/*
@tailwind base;
@tailwind components;
@tailwind utilities;
*/

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(to right, #8e2de2, #4a00e0 );
  color : white;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */

::-webkit-scrollbar-track {
  border-radius: 10px;
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: gray;
  border-radius: 10px;
  position: absolute;
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: gray;
}

.container {
  display: block;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  text-align: center;
}

li {
  list-style-type: none;
  width : 100%;
  cursor: pointer;
  padding : 15px;
  border-radius : 10px;
}

.container ul li:hover {
  background-color: white;
  color : black;
}

.container ul li:hover a {
  color : black;
}

button {
  width : 90px;
  height : 50px;
  border : none;
  transition: 100ms all;
  background : #1600c2;
  border-radius: 9px;
  color : white;
  cursor : pointer;
  font-size: 17px;
}

button:active {
  -webkit-transform : scale(0.9);
  transform : scale(0.9);
  background-color: #1200a1;
}

.menu {
  display : -webkit-flex;
  display : flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: space-around;
  justify-content: space-around;
  background-color: #190061;
  width : 100%;
}

.card {
  padding : 10px;
  transition : 0.3s ease;
  margin : 50px;
  border-radius : 20px;
  width: 300px;
  height: 200px;
  display : inline-block;
  text-align: center;
  background-color: white;
  color : black;
}

.card:hover {
  background-color: black;
  color: white;
  -webkit-transform: scale(1.07);
  transform: scale(1.07);
}

a {
  text-decoration: none;
  color : white;
}

.menu-btn {
  width: 80px;
  padding: 10px;
}

fieldset {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  width : 500px;
  height: 300px;
  border: 1px solid white;
  margin: auto;
  background-color: black;
  color : white;
  border-radius: 10px;
}

fieldset div {
  margin-bottom: 10px;
}

fieldset .nickname input {
  border : 0.5px solid white;
  padding : 15px;
  border-radius: 6px;
  background-color: white;
  color : black;
}

input:focus {
  outline: none;
}

.community-card {
  width : 400px;
  height: 400px;
  background-color: white;
  color : black;
  padding: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  border-radius: 20px;
  margin: auto 10px;
  margin-top : 10px;
}

.community-card span {
  margin-bottom : 5px;
}

.community-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
}

.info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
}

.info span {
  margin-top : 8px;
}

footer {
  background : #05386B;
  width: 100%;
  font-size: 15px;
  position: fixed;
  margin-top: 100px;
  bottom: 0;
}

.register form {
  height: 650px;
  width : 600px;
  margin: auto;
  margin-top: 50px;
  background-color: white;
  color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-flex-direction: column;
  flex-direction: column;
  border-radius: 20px;
}

.register form input {
  width : 260px;
  height : 40px;
  border : 2px solid #12009c;
  border-radius: 7px;
  text-align: center;
  margin: 6px;
}

.info span {
  white-space: nowrap;
}

@media only screen and (max-width: 600px) {
  body {
    zoom : 70%;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.typewriter {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .15em solid black; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em; /* Adjust as needed */
  -webkit-animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-start infinite;
  animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-start infinite;
}

/* The typing effect */

@-webkit-keyframes typing {
  from { width: 0
  }

  to { width: 50%
  }
}

@keyframes typing {
  from { width: 0
  }

  to { width: 50%
  }
}

/* The typewriter cursor effect */

@-webkit-keyframes blink-caret {
  from, to { border-color: transparent
  }

  50% { border-color: black;
  }
}

@keyframes blink-caret {
  from, to { border-color: transparent
  }

  50% { border-color: black;
  }
}
  
.register-alert {
  width : 500px;
  height: 50px;
  background-color: red;
  color: white;
  text-align: center;
  border-radius: 7px;
}
